.task-card
{
    width: 20rem;
    height: 20rem;
    margin: 1rem;
    vertical-align: top;
    display: -ms-inline-flexbox;
    display: inline-flex;
    -ms-flex-align: stretch;
        align-items: stretch;
    -ms-flex-negative: 0;
        flex-shrink: 0;
}

.task-card-card
{
    width: 100%;
    height: 100%;
}

.task-card-edit-button
{
    position: absolute;
    top: 0;
    right: 0;
}

.task-card-edit-form
{
    padding: 0 2rem;
}


